
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse' 
import backTop from '@/components/BackTop/backTop'
import showMore from '@/components/ShowMore/showMore'  
export default{
data() {
return {
cfid: "",
ny: "",
info: {},
drugListInfo: [],
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.cfid = routerParams.ID;
this.ny = routerParams.NY;
this.getOutpatientPrescription();
},

getOutpatientPrescription() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "outpatientDepartmentLog",
"MSH.2": "cf",
"cfid": this.cfid,
"ny": this.ny,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
console.log(res);
if (typeof res != 'undefined') {
if (typeof res.info != 'undefined') {
this.info = res.info;
}
if(typeof res.drugListInfo != 'undefined' && res.drugListInfo.length > 0){
this.drugListInfo = res.drugListInfo;
}
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
},
components: {
XHeader,Collapse,backTop,showMore
},
}




